import React from 'react'

export default function ButtonCyber({text, url, bonusClass, after}) {
    let ButtonTag = url ? 'a' : 'button';
    return (
        <>
            <ButtonTag {...(url && {href: url})} data-after={after} className={"btn-cyber " + bonusClass}>
                <span className="btn-cyber__text" data-text={text}>{text}</span>
                <span className="btn-cyber--glitch"></span>
            </ButtonTag>
        </>
    )
}
