import React from 'react'
import {MeshDistortMaterial, useGLTF} from '@react-three/drei'

export default function Boys(props) {
    const {nodes} = useGLTF("/models/boys.glb");
    return (
        <group {...props} dispose={null}>
            <mesh
                geometry={nodes.Boy.geometry}
                material={nodes.Boy.material}
                position={[0, -0.02, 0]}>
                <meshBasicMaterial wireframe />
            </mesh>
            <mesh geometry={nodes.Boy.geometry}>
                <MeshDistortMaterial color="#ffffff" flatShading roughness={1} metalness={0.5} factor={1} speed={2} />
            </mesh>
            <mesh
                geometry={nodes.Boy1.geometry}
                material={nodes.Boy1.material}
                position={[0.56, -0.02, -0.46]}
                rotation={[0, -0.2, 0]}
            />
            <mesh
                geometry={nodes.Boy2.geometry}
                material={nodes.Boy2.material}
                position={[-0.56, -0.02, -0.48]}
                rotation={[0, 0.24, 0]}
            />
        </group>
    )
}
