import React, {useLayoutEffect} from "react";
import anime from "animejs";
import ButtonCyber from "../../elements/ButtonCyber";
import Vimeo from '@u-wave/react-vimeo';

export default function HeroBlock() {
    useLayoutEffect(() => {
        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            targets: 'canvas',
            easing: 'linear',
            opacity: [0, 1],
            scale: [0.5, 1],
            duration: 400,
        });
        timeline.add({
            targets: '.css--fadedtext',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            translateX: ["-100%", 0],
            duration: 500
        });
    }, []);

    return (
        <div id="outsource" className="md:container mx-auto min-h-screen flex justify-center align-middle relative px-4">
            <div className="grid grid-cols-1 lg:grid-cols-12 pt-32 md:pt-0">
                <div className="lg:col-span-6 lg:order-1 order-2 flex justify-center items-baseline flex-col py-12 lg:py-0">
                    <div className="flex flex-col justify-center relative text-center sm:text-left w-full lg:w-auto">
                        <h1 className="css--outsourcing-head-title font-bold lg:text-5xl text-3xl mb-7 text-left" style={{zIndex: 2}}>Scalability Comes From Outsourcing Your IT Needs to Expert Team</h1>
                    </div>
                    <div className="css--outsourcing-head-subtitle text-white lg:text-xl text-lg w-full font-semibold text-left mb-3.5">Our goal is to satisfy <span className='css--text-fade border-fade'>YOUR</span> business needs by offering our flexible outsourcing models</div>
                    <div className="css--outsourcing-head-subtitle text-white lg:text-xl text-lg w-full font-semibold text-left mb-3.5">We give guarentees.</div>
                    <div className="css--outsourcing-head-btn flex lg:justify-start justify-center w-full mt-11">
                        <ButtonCyber after={'K2'} url={"#models"} bonusClass={''} text={"Explore models"}/>
                    </div>
                </div>
                <div className="col-span-6 flex lg:order-2 order-1 justify-center items-center">
                    <div className="css--video-outsourcing lg:ml-7 lg:w-full p-3.5 lg:p-7 css--animated-border">
                        <Vimeo
                            video="778120864"
                            autoplay={true} controls={false} showTitle={false}
                        />
                    </div>
                </div>
            </div>
        </div>
    )
}
