import '../i18n/i18n';

import React from "react"
import Navigation from "../components/layout/Navigation";
import Outsourcing from "../components/layout/Outsourcing"
import {SEO} from "../components/seo";

export default function AboutUs() {
    return (
        <>
            <noscript
                dangerouslySetInnerHTML={{
                    __html: `<img height="1" width="1" style="display:none"
                     src="https://www.facebook.com/tr?id=549935586969593&ev=PageView&noscript=1"/>
                    `,
                }}
            />
            <main className="main-wrapper relative">
                <Navigation/>
                <section className="pb-16">
                    <Outsourcing/>
                </section>
            </main>
        </>
    )
}

export const Head = () => <>
    <SEO title={"We offer Outsourcing"}
         description={'We are a software development company and we have built multiple digital products for our customers and for ourselves. We have experience in developing tailored solutions to meet client requirements in a short amount of time. Moreover, we would like to point out that we have done it by using various technologies and approaches which shows our flexibility and skill set.'}/>
    <script
        dangerouslySetInnerHTML={{
            __html: `
                    !function(f,b,e,v,n,t,s)
                    {if(f.fbq)return;n=f.fbq=function(){n.callMethod?
                    n.callMethod.apply(n,arguments):n.queue.push(arguments)};
                    if(!f._fbq)f._fbq=n;n.push=n;n.loaded=!0;n.version='2.0';
                    n.queue=[];t=b.createElement(e);t.async=!0;
                    t.src=v;s=b.getElementsByTagName(e)[0];
                    s.parentNode.insertBefore(t,s)}(window, document,'script',
                    'https://connect.facebook.net/en_US/fbevents.js');
                    fbq('init', '549935586969593');
                    fbq('track', 'PageView');
                    `,
        }}
    />
</>
