import React from "react";
import 'react-slideshow-image/dist/styles.css';
import Calendly from "../../elements/Calendly";

export default function Meeting() {
    return (
        <div className="md:container mx-auto md:min-h-screen flex flex-col justify-center align-middle relative pt-24 lg:pt-0 px-4">
            <h1 className="text-3xl md:text-5xl glitch text-center mb-0" data-glitch="Schedule your strategy call below">
                <span className="title-underline">
                    Schedule your strategy call below
                </span>
            </h1>
            <div className="flex justify-center">
                <div className="w-full lg:w-auto">
                    <div className="css--animated-border">
                        <Calendly calendlyURL={"https://calendly.com/kalmars-meeting/30min?embed_domain=kalmars.lv&embed_type=Inline&hide_event_type_details=1&background_color=0b0c15&text_color=ffffff&primary_color=2fc7e5"} bonusClass={"css--outsourcing-calendly"} />
                    </div>
                    <div className="text-white mt-6">
                        <div className="text-center text-base font-medium">
                            Cant find your time?
                        </div>
                        <div itemScope itemType="https://schema.org/Organization" className="flex justify-center font-medium text-xs mt-5">
                            <div className="flex mr-2 items-center">
                                <img className="h-6 w-8 mr-2" src={"/images/icons/email.svg"} height="26" width="36" alt="Email Logo"/>
                                <a itemProp="email" className="text-white" href="mailto:it@kalmars.lv">it@kalmars.lv</a>
                            </div>
                            <div className="flex ml-2 items-center">
                                <img className="h-6 w-8 mr-2" src={"/images/icons/phone.svg"} height="26" width="36" alt="Phone Logo"/>
                                <a itemProp="telephone" className="text-white" href="tel:+37127797931">+371 27797931</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}