import React, {useLayoutEffect} from "react";
import anime from "animejs";
import HeroBlock from "./Hero";
import Bonuses from "./Bonuses";
import Models from "./Models";
import Offers from "./Offers";
import FadedText from "../../elements/FadedText";
import Testemonials from "./Testemonials";
import Meeting from "./Meeting";
import SeperatorLine from "../../elements/SeperatorLine";

const Outsourcing = () => {

    useLayoutEffect(() => {
        require('../../../scss/outsourcing.scss');

        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            targets: '.css--fadedtext',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            translateX: ["-100%", 0],
            duration: 500
        });
    }, []);

    return (
        <>
            <FadedText text={'DISCOVER'} direction="bottom"/>
            <HeroBlock/>
            <SeperatorLine/>
            <Bonuses/>
            <Models/>
            <Offers/>
            <Testemonials/>
            <SeperatorLine/>
            <Meeting/>
        </>
    )
}
export default Outsourcing;
